'use strict';

app.sliderImages = {
	sliderElement: '.js-slider-images',
	init: function init() {
		var _this = this;

		app.common.initScript('swiper.min', 'Swiper', function () {
			_this.lazyLoad();
			_this.runSlider();
		});
		app.common.initStyle('swiper.min');
	},
	lazyLoad: function lazyLoad() {
		var self = this;
		app.common.initScript('lazyload.min', 'LazyLoad', function () {
			app.lazyLoadSliderImagesInstance = new LazyLoad({
				threshold: 50,
				elements_selector: self.sliderElement + ' .lazy-load'
			});
		});
	},
	runSlider: function runSlider() {
		var $slider = $(this.sliderElement);

		if ($slider.length) {
			var slider = new Swiper(this.sliderElement, this.options);

			slider.on('init', function () {
				$slider.addClass('is-loaded');
			});

			slider.init();
		}
	},

	options: {
		init: false,
		loop: true,
		slidesPerView: 1,
		slidesPerGroup: 1,
		watchSlidesVisibility: true,

		navigation: {
			nextEl: '.js-slider-images__button-next',
			prevEl: '.js-slider-images__button-prev'
		},

		on: {
			slideChangeTransitionEnd: function slideChangeTransitionEnd() {
				if (app.lazyLoadSliderImagesInstance) {
					app.lazyLoadSliderImagesInstance.update();
				}
			}
		}
	}
};