'use strict';

app.promo = {
	initEl: '.js-promo',
	init: function init() {
		if ($(this.initEl).length) {
			this.copyWidget();
		}
	},
	copyWidget: function copyWidget() {
		var $el = $(this.initEl);
		var $widgetPromo = $('.widget-promo', $el);

		$widgetPromo.closest('.promo__item').addClass('promo__item_sm-hide');

		$widgetPromo.clone().prependTo($el.parent()).wrap('<div class="promo-entry promo-entry_sm"></div>');
	}
};