'use strict';

app.popularSlider = {
	sliderElement: '.js-popular-slider',
	init: function init() {
		var _this = this;

		app.common.initScript('swiper.min', 'Swiper', function () {
			_this.lazyLoad();
			_this.runSlider();
		});
		app.common.initStyle('swiper.min');
	},
	lazyLoad: function lazyLoad() {
		var self = this;
		app.common.initScript('lazyload.min', 'LazyLoad', function () {
			app.lazyLoadNewsSliderInstance = new LazyLoad({
				threshold: 50,
				elements_selector: self.sliderElement + ' .lazy-load',
				class_loaded: 'image-loaded'
			});
		});
	},
	runSlider: function runSlider() {
		var $slider = $(this.sliderElement);

		if ($slider.length) {
			$.each($slider, function () {
				var $curSlider = $(this);
				var $nextEl = $curSlider.find('.js-popular-slider__button-next');
				var $prevEl = $curSlider.find('.js-popular-slider__button-prev');

				var sliderInstance = new Swiper($curSlider.find('.swiper-container'), {
					init: false,
					loop: true,
					slidesPerView: 3,
					slidesPerGroup: 3,
					spaceBetween: 30,
					watchSlidesVisibility: true,
					lazy: true,

					navigation: {
						nextEl: $nextEl,
						prevEl: $prevEl
					},

					breakpoints: {
						767: {
							slidesPerView: 2,
							slidesPerGroup: 2,
							spaceBetween: 20
						},
						575: {
							slidesPerView: 1,
							slidesPerGroup: 1,
							spaceBetween: 16
						}
					},

					on: {
						slideChangeTransitionEnd: function slideChangeTransitionEnd() {
							if (app.lazyLoadNewsSliderInstance) {
								app.lazyLoadNewsSliderInstance.update();
							}
						}
					}
				});

				sliderInstance.on('init', function () {
					$curSlider.addClass('is-loaded');
				});

				sliderInstance.init();
			});
		}
	}
};