'use strict';

app.preloader = {
	show: function show() {
		var container = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : $('body');
		var type = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 'static';
		var size = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : 'normal';

		// type: static, cover, fit
		// size: normal, small, large
		var $container = $(container);
		if ($container.has('.js-preloader').length) {
			$('.js-preloader').addClass('active');
		} else {
			var tpl = '<div class="preloader js-preloader preloader_' + type + '">\n\t\t\t\t\t\t<div class="preloader__figure preloader__figure_' + size + '"></div>\n\t\t\t\t\t</div>';
			if (type === 'fit' && $container.css('position') === 'static') {
				$container.css('position', 'relative');
			}
			$(tpl).clone().appendTo($container).addClass('active');
		}
	},
	hide: function hide() {
		var preloader = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : null;

		var $preloader = $(preloader);
		if (preloader) {
			$preloader.removeClass('active');
		} else {
			$('.js-preloader').removeClass('active');
		}
	}
};