'use strict';

app.formElement = {
	togglePassword: function togglePassword() {
		var showPassBtnElement = '.js-show-pass';
		var showPassActiveClass = 'is-show';

		if ($(showPassBtnElement).length) {
			$('body').on('click', showPassBtnElement, function (e) {
				e.preventDefault();

				var $curBtn = $(this);
				var $curInput = $curBtn.parent().find('input');

				$curInput.attr('type', 'text');

				if (!$curBtn.hasClass(showPassActiveClass)) {
					$curBtn.addClass(showPassActiveClass);
					$curInput.attr('type', 'text');
				} else {
					$curBtn.removeClass(showPassActiveClass);
					$curInput.attr('type', 'password');
				}
			});
		}
	},
	confirmPassword: function confirmPassword() {
		var $pass1 = $('.js-main-password');
		var $pass2 = $('.js-confirm-password');

		function matchPasswords(pass1, pass2) {
			var val1 = pass1.val();
			var val2 = pass2.val();
			var $pass = $pass1.add($pass2);
			$pass.toggleClass('confirm-error', val1 !== val2).toggleClass('confirm-success', val1 === val2);
		}

		if ($pass1.length && $pass2.length) {
			var timeout1 = void 0;
			$pass1.keyup(function () {
				var $curPassword = $(this);
				clearTimeout(timeout1);
				timeout1 = setTimeout(function () {
					matchPasswords($curPassword, $curPassword.closest('form').find($pass2));
				}, 300);
			});

			var timeout2 = void 0;
			$pass2.keyup(function () {
				var $curPassword = $(this);
				clearTimeout(timeout2);
				timeout2 = setTimeout(function () {
					matchPasswords($curPassword, $curPassword.closest('form').find($pass1));
				}, 300);
			});
		}
	}
};