'use strict';

app.checkout = {
	formEl: '.js-checkout-from',
	cardEl: '.js-checkout-card',
	dateCheckEl: '.js-choose-date',
	datepickerEl: '.js-checkout-datepicker',
	btnPayEl: '.js-btn-pay',
	btnOrderEl: '.js-btn-order',
	init: function init() {
		var self = this;
		app.common.initScript('flatpickr.min', 'flatpickr', function () {
			var el = document.querySelectorAll(self.dateCheckEl);
			if (el.length) {
				self.createDatepicker();
				self.openDatepicker();
				self.steps();
			}
		});
		app.common.initStyle('flatpickr.min');
	},
	createDatepicker: function createDatepicker() {
		var self = this;
		var $el = $(self.datepickerEl);
		var $buffer = $('<div>', {
			class: 'input-buffer'
		});
		$.each($el, function () {
			var $curEl = $(this);
			$buffer.clone().prependTo($curEl).hide();

			var $butter = $curEl.find('.input-buffer');
			var $curInput = $curEl.find('input');

			$butter.text($curInput.attr('placeholder'));
			$curInput.width($butter.width());

			$curEl.flatpickr({
				wrap: true,
				dateFormat: 'm.d.Y',
				locale: {
					firstDayOfWeek: 1,
					weekdays: {
						shorthand: ['Вс', 'Пн', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб'],
						longhand: ['Воскресенье', 'Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница', 'Суббота']
					},
					months: {
						shorthand: ['Янв', 'Фев', 'Март', 'Апр', 'Май', 'Июнь', 'Июль', 'Авг', 'Сен', 'Окт', 'Ноя', 'Дек'],
						longhand: ['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь']
					}
				},
				onOpen: function onOpen() {
					$curEl.closest('label').find(self.dateCheckEl).prop('checked', true);
				},
				onChange: function onChange() {
					$butter.text($curInput.val());
					$curInput.width($butter.width());
				}
			});
		});
	},
	openDatepicker: function openDatepicker() {
		var self = this;
		$('body').on('change', self.dateCheckEl, function () {
			if ($(this).prop('checked')) {
				$(this).closest('label').find(self.datepickerEl).find('input').focus();
			}
		});
	},
	steps: function steps() {
		var self = this;
		$('body').on('click', self.btnOrderEl, function (e) {
			e.preventDefault();
			var $curBtn = $(this);
			var $curForm = $curBtn.closest(self.formEl);
			$curForm.find(self.cardEl).show();
			$curForm.find(self.btnPayEl).show();
			$curBtn.hide();
		});
	}
};