'use strict';

app.formSelect = {
	initElement: '.js-form-select',
	init: function init(selector) {
		var _this = this;

		app.common.initScript('select2.full.min', 'select2', function () {
			var el = document.querySelectorAll(selector || _this.initElement);
			if (el.length) {
				_this.run(el);
			}
		});
		app.common.initStyle('select2.min');
	},
	run: function run(el) {
		for (var i = 0; i < el.length; i++) {
			var curEl = el[i];
			var dataSearch = curEl.getAttribute('data-search');
			var showSearch = dataSearch === null || dataSearch === 'false' ? Infinity : 0;
			$(curEl).select2({
				theme: 'custom',
				language: 'ru',
				width: '100%',
				minimumResultsForSearch: showSearch,
				containerCssClass: 'form-select__selection',
				dropdownCssClass: 'form-select__dropdown'
			});
		}
	}
};