'use strict';

app.filters = {
	initEl: '.js-filters',
	filtersItemEl: '.js-filters-li',
	filtersPanelEl: '.js-filters-drop',
	filtersSwitcherEl: '.js-filters-arrow',
	filterEl: '.js-filter',
	switcherFiltersEl: '.js-filters-menu-switcher',
	init: function init() {
		var _this = this;

		app.common.initScript('jquery.accordion-simple', 'accordionSimple', function () {
			if ($(_this.initEl).length) {
				_this.events();
			}
		});

		app.common.initScript('jquery.switch-class', 'switchClass', function () {
			var $switcherEl = $(_this.switcherFiltersEl);
			if ($switcherEl.length) {
				_this.toggleFilterPanel(_this.switcherFiltersEl);
			}
		});
	},
	events: function events() {
		var self = this;
		var $filters = $(this.initEl);
		var filtersAccordion = $filters.accordionSimple({
			// Elements
			block: self.filtersItemEl,
			panel: self.filtersPanelEl,
			switcher: self.filtersSwitcherEl,
			// Additional settings
			collapsed: false,
			duration: 200
		});

		$filters.on('click', self.filtersItemEl + ' > a', function (e) {
			// Prevent click if need
			e.preventDefault();
			var $curEl = $(this);
			var $curItem = $curEl.closest(self.filtersItemEl);

			// Show preloader
			app.preloader.show($curEl.closest(self.initEl).parent(), 'fit', 'small');

			// setTimeout for example
			setTimeout(function () {
				if ($curItem.hasClass('current')) {
					$(self.filterEl, $curItem).prop('checked', false);

					// Filtered...

					$curItem.removeClass('current').find(self.filtersItemEl).removeClass('current');
				} else {
					// Filtered...

					// Then show filters
					var $drop = $curEl.siblings(self.filtersPanelEl);
					if ($drop.length) {
						filtersAccordion.accordionSimple('open', $drop, function () {
							$curItem.addClass('current');
						});
					} else {
						$curItem.addClass('current');
					}
				}

				// Hide preloader
				app.preloader.hide();
			}, 500);
		});

		$filters.on('change', self.filterEl, function () {
			var $curEl = $(this);

			// Show preloader
			app.preloader.show($curEl.closest(self.initEl).parent(), 'fit', 'small');

			// setTimeout for example
			setTimeout(function () {
				$curEl.parentsUntil(self.initEl).filter(self.filtersItemEl).addClass('current');

				// Hide preloader
				app.preloader.hide();
			}, 500);
		});
	},
	toggleFilterPanel: function toggleFilterPanel(el) {
		var $switcherEl = $(el);
		var $html = $('html');
		$switcherEl.switchClass({
			removeExisting: true,
			switchClassTo: $('.js-filters-menu').add('.js-filters-menu-overlay'),
			removeEl: $('.js-filters-menu-close').add('.js-filters-menu-overlay'),
			cssScrollFixed: true,
			preventRemoveClass: 'js-prevent-hide',
			modifiers: {
				activeClass: 'filters-menu-is-open'
			},
			afterAdd: function afterAdd() {
				$html.addClass('open-only-mob');
			},
			afterRemove: function afterRemove() {
				$html.removeClass('open-only-mob');
			}
		});
	}
};